/**
 * Log levels for controlling which messages are displayed
 */
export enum LogLevel {
  DEBUG = 0,
  INFO = 1,
  WARNING = 2,
  ERROR = 3,
}

import { captureError, type ErrorContext } from './sentryClient'

const isDev = import.meta.env.MODE === 'development'

/**
 * A simple logger class with configurable log levels and prefixes
 * - Development: console output for all levels
 * - Production: silent for debug/info/warn, Sentry reporting for errors
 */
export class Logger {
  private level: LogLevel
  private prefix: string
  private context: ErrorContext

  constructor(prefix: string = '', context: ErrorContext = 'content') {
    this.prefix = prefix
    this.context = context

    // Read log level from environment variable, default to INFO if not set or invalid
    const envLevel = import.meta.env.VITE_LOGGER_LEVEL
    const levelNum = envLevel !== undefined ? parseInt(envLevel, 0) : LogLevel.INFO
    this.level = levelNum >= LogLevel.DEBUG && levelNum <= LogLevel.ERROR ? levelNum : LogLevel.INFO
  }

  /**
   * Sets the minimum log level for messages to be displayed
   * @param level - The log level ('debug', 'info', 'warning', or 'error')
   */
  public setLevel(level: 'debug' | 'info' | 'warning' | 'error') {
    this.level = LogLevel[level.toUpperCase() as keyof typeof LogLevel]
  }

  /**
   * Internal method to handle logging with timestamp and level filtering
   * @param level - The log level of this message
   * @param consoleMethod - The console method to use ('log', 'warn', or 'error')
   * @param args - The arguments to log
   */
  private log(level: LogLevel, consoleMethod: 'log' | 'warn' | 'error', ...args: any[]) {
    if (level >= this.level) {
      const timestamp = new Date().toISOString()
      console[consoleMethod](`[${timestamp}] ${this.prefix}`, ...args)
    }
  }

  /**
   * Logs a debug message
   * - Development: console.log
   * - Production: silent
   * @param args - The arguments to log
   */
  public debug(...args: any[]) {
    if (isDev) {
      this.log(LogLevel.DEBUG, 'log', '[DEBUG]', ...args)
    }
  }

  /**
   * Logs an info message
   * - Development: console.log
   * - Production: silent
   * @param args - The arguments to log
   */
  public info(...args: any[]) {
    if (isDev) {
      this.log(LogLevel.INFO, 'log', '[INFO]', ...args)
    }
  }

  /**
   * Logs a warning message
   * - Development: console.warn
   * - Production: silent (warnings are not reported to Sentry by default)
   * @param args - The arguments to log
   */
  public warn(...args: any[]) {
    if (isDev) {
      this.log(LogLevel.WARNING, 'warn', '[WARN]', ...args)
    }
  }

  /**
   * Logs an error message
   * - Development: console.error
   * - Production: silent + report to Sentry
   * @param message - The error message
   * @param error - Optional Error object or unknown (from catch blocks)
   * @param extra - Optional extra context data
   */
  public error(message: string, error?: unknown, extra?: Record<string, unknown>) {
    if (isDev) {
      // Development: console output
      this.log(LogLevel.ERROR, 'error', '[ERROR]', message, error, extra)
    } else if (LogLevel.ERROR >= this.level) {
      // Production: silent + report to Sentry
      // Convert unknown to Error if needed
      const err = error instanceof Error ? error : new Error(message)
      captureError(err, this.context, { message, ...extra })
    }
  }
}

/**
 * Default logger instance with '[Moki]' prefix for content scripts
 */
export const logger = new Logger('[Moki]', 'content')

/**
 * Logger instance for popup context
 */
export const popupLogger = new Logger('[Moki:Popup]', 'popup')

/**
 * Logger instance for background service worker
 */
export const backgroundLogger = new Logger('[Moki:BG]', 'background')
