// src/tools/sentryClient.ts

import { BrowserClient, defaultStackParser, makeFetchTransport, Scope } from '@sentry/browser'

const isDev = import.meta.env.MODE === 'development'
const isChromeExtension = typeof chrome !== 'undefined' && chrome.runtime?.getManifest

// Only initialize in production Chrome extension environment
let client: BrowserClient | null = null
let scope: Scope | null = null

if (!isDev && isChromeExtension) {
  client = new BrowserClient({
    dsn: 'https://317212f73d09b39bd6601b48de3e2037@o4509400899125248.ingest.us.sentry.io/4510515352043521',
    transport: makeFetchTransport,
    stackParser: defaultStackParser,
    integrations: [], // Disable all auto-integrations (avoid Service Worker compatibility issues)
    environment: 'production',
    release: chrome.runtime.getManifest().version,
  })

  scope = new Scope()
  scope.setClient(client)
  client.init()
}

export type ErrorContext = 'popup' | 'content' | 'background'

/**
 * Report error to Sentry
 * - Development: console.error only
 * - Production: report to Sentry
 */
export function captureError(
  error: Error | string,
  context: ErrorContext,
  extra?: Record<string, unknown>,
): void {
  const err = error instanceof Error ? error : new Error(error)

  if (isDev) {
    console.error(`[${context}]`, err, extra)
    return
  }

  if (!client || !scope) return

  scope.setTag('context', context)
  if (isChromeExtension) {
    scope.setTag('extension_version', chrome.runtime.getManifest().version)
  }

  if (extra) {
    Object.entries(extra).forEach(([key, value]) => {
      scope!.setExtra(key, value)
    })
  }

  // Content Script: add page info
  if (context === 'content' && typeof location !== 'undefined') {
    scope.setExtra('url', location.href)
    const videoId = location.href.match(/[?&]v=([^&]+)/)?.[1]
    if (videoId) scope.setExtra('videoId', videoId)
  }

  scope.captureException(err)
}

/**
 * Set user info (call after login)
 */
export function setUser(userId: string, email?: string): void {
  if (scope) {
    scope.setUser({ id: userId, email })
  }
}

/**
 * Clear user info (call after logout)
 */
export function clearUser(): void {
  if (scope) {
    scope.setUser(null)
  }
}
